    <!--购物车-->
    <van-popup v-model="addChartVisible" position="bottom">
      <div class="addChart">
        <a class="iconfont close" @click="addChartVisible = false">
          &#xe66c;
        </a>
        <div class="pro clearfix">
          <div class="imgWrap">
            <img :src="chartData.spuMainImage" class="img" />
          </div>
          <div class="rightContent">
            <div>
              <span class="price"> {{ chartData.price }} AED </span>
              <span class="tips_vip"> VIP </span>
              <span class="tips_cutOff"> {{ chartData.cutOff }} OFF</span>
            </div>
            <div>
              <span> {{ chartData.hisPrice }} AED</span>
            </div>
            <div><span class="smallRed"> Only 5 </span></div>
