          </div>
        </div>
        <div class="sizeChoose">
          <p class="tittle">Color</p>
          <p class="blocks" v-if="specData.productAttributeList">
            <a
              :class="index == colorSelect ? 'active' : ''"
              v-for="(item, index) in specData.productAttributeList[0]
                .productAttributeOptionList"
              :key="index"
              @click="colorSelect = index"
