    <!-- 回到顶部 -->
    <div class="go-top iconfont" v-if="btnFlag" @click="backTop">
      &#xe612;
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { getSelection } from "@/api/index";
import { addCart } from "@/api/cart";
export default {
  name: "home",
  data() {
    return {
      btnFlag: false,
      show: false,
      // 购物数量
      num: 1,
      colorSelect: null,
      sizeSelect: null,
      // 规格属性
      skuCode: null,
      searchValue: "",
      bannerSwiperOption: {
        autoplay: true,
        loop: true,
        slidesPerView: 1.2,
        spaceBetween: 0,
        centeredSlides: true,
        onClick: function(swiper, event) {
          console.log(swiper, event);
        }
      },
      flashSwiperOption: {
        slidesPerView: 3.5,
        spaceBetween: 10,
        freeMode: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      commonSwiperOption: {
        slidesPerView: 2.3,
        spaceBetween: 10,
        freeMode: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      addChartVisible: false,
      shareVisible: false,
      timer: {
        d: "00",
        h: "00",
        m: "00",
        s: "00"
      },
      chartData: {},
      colorList: [
        { color: "White", bol: true },
        { color: "Black", bol: false },
        { color: "Red", bol: false },
        { color: "Blue", bol: false }
      ],
      //规格数据
      specData: {}
    };
  },
  computed: {
    ...mapGetters(["indexData", "productData", "user", "categoryOne"])
  },
  created() {
    //console.log(this.productData.flashSale.productList)
  },
  mounted() {
    window.addEventListener("scroll", this.scrollToTop);
    this.init();
  },
  destroyed() {
    window.addEventListener("scroll", this.scrollToTop);
  },
  methods: {
    init() {
      let t = this;

      this.$store.dispatch("productData").then(r => {
        this.timer.remain = r.flashSale.endTimeStr * 1;
        t.countTime();
      });
    },
    countTime() {
      let leftTime = this.timer.remain;
      if (leftTime >= 0) {
        this.timer.d = this.timeStr(Math.floor(leftTime / 60 / 60 / 24));
        this.timer.h = this.timeStr(Math.floor((leftTime / 60 / 60) % 24));
        this.timer.m = this.timeStr(Math.floor((leftTime / 60) % 60));
        this.timer.s = this.timeStr(Math.floor(leftTime % 60));
        this.timer.remain = this.timer.remain - 1;
      }

      //递归每秒调用countTime方法，显示动态时间效果
      setTimeout(this.countTime, 1000);
    },
    timeStr(count) {
      if (count < 10) {
        return "0" + count;
      } else {
        return count;
      }
    },
    // 弹出规格
    addChart(data) {
      this.addChartVisible = true;
      this.chartData = data;
      getSelection(data.spuCode).then(res => {
        this.specData = res.data.result;
      });
    },
    //添加到购物车
    addChartGo() {
      this.$toast("Please select color and size etc");
      //this.addChartVisible=false;
    },
    // 商品颜色选择
    colorChange(i) {
      // this.colorList.map(item=>{
      //   item.bol=false;
      // })
      // this.colorList[i].bol=true;
    },
    //加入购物车数量
    jian() {
      this.num--;
      if (this.num <= 1) {
        this.num = 1;
      }
    },
    jia() {
      this.num++;
    },
    //添加到购物车
    addCartCom() {
      if (this.colorSelect != null && this.sizeSelect != null) {
        let json = `[{"${this.specData.productAttributeList[0].id}":"${
          this.specData.productAttributeList[0].productAttributeOptionList[
            this.colorSelect
          ].id
        }"},{"${this.specData.productAttributeList[1].id}":"${
          this.specData.productAttributeList[1].productAttributeOptionList[
            this.sizeSelect
          ].id
        }"}]`;
        let list = this.specData.productSkuList;

        for (let i = 0; i < list.length; i++) {
          if (list[i].attributeJson === json) {
            this.skuCode = list[i].skuCode;
          }
        }
      }
      if (this.skuCode == null) {
        this.$toast("Please Select Specification");
      } else {
        addCart("", this.skuCode, this.num).then(res => {
          if (res.data.msg == "success") {
            this.$toast("Add Successfully");
          }
        });
      }
    },
    shareShow(data) {
      this.shareVisible = true;
    },
    goDetail(spuCode) {
      this.$router.push({
        path: "/proDetail",
        query: { spuCode: spuCode }
      });
    },
    goCart() {
      this.$router.push({
        path: "/cart",
        query: {}
      });
    },
    changeBanner(e) {
      let index = this.$refs.bannerSwiper.swiper.clickedIndex;
      this.to(this.indexData.bannerAdList[index].url);
    },
    chooseBest(e) {
      console.log(e);
      let index = this.$refs.bestSaleSwiper.swiper.clickedIndex;
      this.goDetail(this.productData.bestSales[index].spuCode);
    },
    chooseDetail(spoCode){
        this.goDetail(spoCode);
    },
    to(url) {
      window.location.href = url;
    },
    //回到顶部
    backTop() {
      let that = this;
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 5);
        document.documentElement.scrollTop = document.body.scrollTop =
          that.scrollTop + ispeed;
        if (that.scrollTop === 0) {
          clearInterval(timer);
        }
      }, 16);
    },
    scrollToTop() {
      let that = this;
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      that.scrollTop = scrollTop;
      if (that.scrollTop > 60) {
        that.btnFlag = true;
      } else {
        that.btnFlag = false;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../common/css/home.scss";
.go-top {
  position: fixed;
  text-align: center;
  right: 10px;
  bottom: 30%;
  z-index: 1001;
  background-color:rgba(255,255,255,.7);
  border:solid 1px #959595;
  color:#959595;
  width: 30px;
  height: 30px;
  line-height: 30px;
  border-radius: 100%;
}
</style>



